.PostContainer{
    width: 70%;  
    display:flex;
    justify-content: center;
    align-items: center;
    margin:auto;

}

.Posts{
    width:100%;
    height: 100%;
    min-height:100vh;
    /* background:url('./Images/Background.jpg'); */
    background-attachment: fixed;
    background-size: cover;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin:5% auto;
    flex-direction: column;
}

.PostImg{
    width:90%;
    margin:auto;
    display: flex;
    align-items: center;
    border-radius: 8px;
    /* position: absolute; */
    margin: 30px auto;
    transition: 0.5s;
    cursor:grab;
}
.PostImg:hover{
    transform: scale(1.1);
}

.Post{
    width:60%;
    display:flex;
    justify-content:space-between;
    flex-direction:column;
    margin:5% auto;
    padding:20px;
    border-radius:10px;
    box-shadow: 5px 5px 5px 5px rgba(217, 212, 212, 0.126);
    background-color:rgba(137, 145, 145, 0.139);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.PostProfile{
    width:auto;
    display:flex;
}

.ProfilePic{
    width:80px;
    height:80px;
    border-radius:100%;
    margin: 10px;
    transition: transform 0.7s ;    
}

.PosterName{
    font-size: x-large;
    text-align: center;
    margin: auto 10px ;
    transition:0.8s;
}

.Role{
    font-size: small;
    font-weight: 400;
    text-align: left;
}

.Liking{
    width: auto;
    display: flex;
    margin: 20px;
}
.LikeStatus{
    font-size: larger;
    margin-left:2%;
}
.LikeImg{
    cursor:grab;
    transition: 0.5s;
}
.LikeImg:hover{
    transform: scale(1.1);
}

@media screen and (max-width:880px) {
    .PostContainer{
        width:90%;
    }
    .Post{
        width:90%;
    }
}