.Container{
    width:100%;
    height:100%;
    min-height:92vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
}
.Heading{
    font-size:60px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.HomeDiscription {
    width:60%;
    margin: auto 20px;
    padding: 10px;
    transition: 2s;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.Text{
    font-size: x-large;
}

.HomeImg{
    width: 65%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto 20px;
    transition: 0.5s;
}
.HomeImg:hover{
    transform:skew(2deg);
}

.btn{
    width:100px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: large;
    padding: 10px;
    background-color: #a3a3a378;
    margin: 20px auto;
    border-radius: 7px;
    border: 1px solid rgb(255, 255, 255);
    transition: 0.5s;
    cursor: pointer;
    display: flex;
    justify-content:center;
    align-items: center;
}

.btn:hover{
    transform: scale(1.1);
    background-color: #25252522;
}

@media screen and (max-width:880px) {

.Container{
    width:100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    }
    .Heading{
        font-size: 40px;
    }
    .HomeDiscription{
        width:80%;
    }
    .HomeImg{
        width: 80%;

    }
}