.ContactContainer {
    width: 100%;
    height: 100%;
    min-height: 92vh;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.Contact {
    width: 80%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    margin: auto;
}


.ContactForm {
    width: 50%;
    margin: auto;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.233);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    border-radius: 10px;
}

.Input,
.Message {
    width: 60%;
    height: 30px;
    margin: 10px auto;
    resize: none;
    border-radius: 10px;
    border: 1px solid black;
    transition: 0.5s;
}

.Message {
    height: 100px;
}

.Input:hover,
.Message:hover {
    transform: scale(1.05);
}

.FeedbackSubmit {
    width: 100px;
    margin: auto;
    background-color: rgba(143, 143, 145, 0.792);
    color: aliceblue;
}

.ContactImg {
    width: 40%;
    cursor: grab;
}

.SocialMedia {
    width: 40%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    transition: 0.5s;
}

.FooterLinkImg {
    transition: 0.5s;
}

.FooterLinkImg:hover {
    transform: scale(1.1);
}

@media screen and (max-width:880px) {
    .ContactContainer {
        width: 100%;
        height:100%;
        min-height:85vh;
        }

    .Contact {
        width: 90%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
    }

    .ContactForm {
        width: 90%;
    }

    .ContactImg {
        display: none;
    }

    .SocialMedia {
        width: 85%;
    }

    .Input,
    .Message {
        width: 80%;
    }


}