*{
    margin: 0%;
}

.HeaderContainer{
    width:100%;
    height: 5vh;
    display: flex;
    justify-content: space-between;
    align-items: center ;
    background-color: rgba(255, 255, 255, 0.107);
    padding:10px 0px ;   
    position: sticky;
    top:0px

    }

.Logo{
    font-size: xx-large;
    font-family: 'Allura', cursive;
    font-weight: 500;
    margin: auto 10px ;
}
.Links{
    text-decoration: none;
}

.Links::after{
    color:transparent;
}

li{
    transition: 0.5s;
}

li:hover{
    transform:scale(1.1);
}

.Navs{
    width:30%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-decoration: none;
    list-style: none;
    margin: auto 1px ;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: larger;
    padding: 10px;
    
}

.Modebtn{
    background-color:transparent;
    border:0px;
    cursor: pointer;
    
}
@media screen and (max-width:880px) {
    
.HeaderContainer{
    width:100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.Navs{
    width:100%;
}
}